<template>
  <div>
    Here i am !
    <button @click="loggin">Loggin</button>
  </div>
</template>


<script>
  import Pinterest from '../../util/pinterest';

  export default {
    components: {
      Pinterest
    },
    methods: {
      loggin: function(){
        Pinterest.login(this.isLogged)
      },
      isLogged: function(){
        console.log(Pinterest.loggedIn())
      }
    }
  }

</script>


